<template>
	<div>
	  <v-list class="pa-0">
	    <v-list-tile avatar>
	      <v-list-tile-avatar>
	        <img :src="avatarUrl">
	      </v-list-tile-avatar>
	      <v-list-tile-content>
	        <v-list-tile-title>{{ nickname }}</v-list-tile-title>
	      </v-list-tile-content>
	    </v-list-tile>
	  </v-list>
		<v-divider></v-divider>
		<v-list>
	    <v-list-tile
	    v-for="(router, index) in routers"
	    :key="index"
	    :to="{path: router.to}"
	    >
		    <v-list-tile-action>
		      <v-icon>{{ router.icon }}</v-icon>
		    </v-list-tile-action>
		    <v-list-tile-content>
		      <v-list-tile-title>{{ router.title }}</v-list-tile-title>
		    </v-list-tile-content>
		  </v-list-tile>
		</v-list>
	</div>
</template>

<script>

import { mapState } from 'vuex'

  const routers = [
  {
    icon: 'home',
    to: '/',
    title: 'Home'
  },
  {
    icon: "search",
    to: "/browser",
    title: "Browser"
  },
  {
    icon: "star",
    to: "/collection",
    title: "Collection"
  }, {
    icon: "settings",
    to: "/setting",
    title: "Setting"
  }
  ];


export default {

  name: 'nav-drawer-content',

  data () {
    return {
      routers,
    }
  },

  computed: {
  	...mapState({
	  	nickname: state => state.user.nickname,
	  	avatarUrl: state => state.user.avatarUrl,
  	})
  },

  mounted: function() {
  	this.$store.dispatch('user/updateUserInfo')
  }
}
</script>

