<template>
	<v-flex xs6 sm4 pa-1>
		<v-hover>
			<v-card
				slot-scope="{hover}"
				:class="`elevation-${hover?18:2}`"
			>
			<v-img
				:src="coverURL"
				aspect-ratio="1"
			></v-img>
			<v-card-text primary-title>
				<h3 class="singleLine">{{ name }}</h3>
				<div class="singleLine">{{ artists }}</div>
				<div class="singleLine grey--text">{{ `${year} / ${size}` }}</div>
			</v-card-text>
			<v-card-actions>
				<v-btn icon @click="markAlbum"><v-icon>star</v-icon></v-btn>
				<v-spacer></v-spacer>
				<router-link :to="routerTo">
					<v-btn icon><v-icon>keyboard_arrow_right</v-icon></v-btn>
				</router-link>
			</v-card-actions>
			<v-snackbar
			  v-model="turnOnAlert"
			>
			  {{ markResult==1?markSuccessMsg: markErrorMsg }}
			  <v-btn flat color="primary" @click.native="turnOnAlert = false">Close</v-btn>
			</v-snackbar>
		</v-card>
	</v-hover>
</v-flex>
</template>

<script>

	import {markLikeService} from '@/services/collection.js'
	export default {

		name: 'album-item',

		props: {
			album: {
				type: Object,
				required: true,
			},
			unlike: {
				type: Boolean,
				required: false,
			},
		},

		data() {
			return {
				turnOnAlert: false,
				markResult: 0, // 0: no alert; 1: success; 2: wrong
				markSuccessMsg: 'Mark Successfully-)',
				markErrorMsg: 'Mark Failed-('
			}
		},

		computed: {
			routerTo: function() {
				return {
					path: '/details/album/'+this.$props.album.id
				}
			},
			id: function() {
				return this.$props.album.id
			},
			name: function() {
				return this.$props.album.name 
			},
			artists: function() {
				return this.$props.album.artist.name
			},
			coverURL: function() {
				return this.$props.album.picUrl 
			},
			size: function() {
				const _size = this.$props.album.size
				return `${_size} ${_size>1?'songs':'song'}`
			},
			type: function() {
				return this.$props.album.type 
			},
			year: function() {
				return (new Date(this.$props.album.publishTime)).getFullYear()
			}
		},

		methods: {
			markAlbum: async function() {
				const host = this.$store.getters.urlHost
				const userId = this.$store.state.userId

				if (!userId) {
					this.markResult = 2; 
					this.turnOnAlert = true;
					return;
				}

				const	resourceId = this.id
				const	typeId = 10
				let markLike;

				if (this.$props.unlike) {
					markLike = false
				} else {
					markLike = true
				}

				try {
					await markLikeService(host, userId, resourceId, typeId, markLike)
					this.markResult = 1;
					this.turnOnAlert = true;
				} catch(error) {
					this.markResult = 2;
					this.turnOnAlert = true;
				}

			}
		},
	}
</script>
