import { getUserInfoService } from '@/services/user.js'

const defaultAvatar = 'https://static.hdslb.com/images/akari.jpg'
const defaultNickname = '...'

const state = {
	nickname: defaultNickname,
	avatarUrl: defaultAvatar,
}

const mutations = {
	_update_user_info (state, { nickname, avatarUrl }) {
		state.nickname = nickname;
		state.avatarUrl = avatarUrl || defaultAvatar;
	}
}

const actions = {
	updateUserInfo({state, rootState, rootGetters, commit}) {
		const userId = rootState.userId;
		const host = rootState.host;

		if (!userId) {
			return;
		}

		getUserInfoService(host, userId)
			.then((json) => {
				commit('_update_user_info', json.info)
			})
			.catch((error) => {
				console.log(error)
			})
	}
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}