<template>
  <v-app :dark="isDarkTheme">
    <!-- Router Drawer -->
    <v-navigation-drawer fixed clipped app v-model="drawer">
      <nav-drawer-content :drawer="drawer"></nav-drawer-content>
    </v-navigation-drawer>
  <!-- Top Toolbar -->
  <v-toolbar app fixed clipped-left :color="toolbarColor">
    <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
    <router-link :to="{path: '/'}">
      <v-btn flat>
        <v-toolbar-title>{{ title }}</v-toolbar-title>
      </v-btn>
    </router-link>
    </v-toolbar>
  <!-- Main Content -->
  <v-content>
    <v-container fluid>
      <v-fade-transition mode="out-in">
        <router-view></router-view>
      </v-fade-transition>
    </v-container>
  </v-content>
  <!-- Bottom Footer -->
  <v-footer>
    <v-spacer></v-spacer>
    <div class="grey--text caption">Powered By Vue-Cli</div>
  </v-footer>
</v-app>
</template>

<script>
  import '@/main.css'
  import { mapState } from 'vuex'
  // import PlayerBar from "@/components/PlayerBar.vue";
  import NavDrawerContent from '@/components/NavDrawerContent.vue'


  export default {
    name: "App",
    data() {
      return {
        title: 'NxMusic',
        drawer: false
      };
    },
    computed: {
      ...mapState({
        isDarkTheme: state => state.isDarkTheme,
        toolbarColor: state => {
          switch (state.searchSource) {
            case 0: return 'indigo';
            case 1: return 'red';
            default: return 'black';
          }
        }
      })
    },
    components: {
    // PlayerBar,
    NavDrawerContent,
  }
};
</script>
