<template>
	<v-container>
		<!-- setting container -->
		<v-layout row wrap>
		  <v-flex xs12 md8>
				<v-form>
					<!-- host setting -->
					<v-text-field
					label="Host"
					outline
					v-model="host"
					@blur.prevent="updateHost"
					></v-text-field>
					<!-- use local/online resource when searching? -->
					<v-select
					outline
					:items="searchSources"
					v-model="searchSource"
					label="Search from Where First?"
					></v-select>
					<!-- use Dark theme? -->
					<v-select
					outline
					:items="themes"
					v-model="isDarkTheme"
					label="App Theme"
					></v-select>
					<!-- Switch to another user EASILY-) -->
					<v-text-field
				  label="Switch To Another User"
				  v-model="userId"
				  outline
				  @blur.prevent="updateUserId"
					></v-text-field>
				</v-form>
		  </v-flex>
		</v-layout>
		<!-- Setting saved alert -->
		<v-snackbar
		v-model="snackbarOn"
		bottom
		right
		:timeout="3000"
		>
		{{ alertMsg }}
		<v-btn flat color="primary" @click.native="snackbarOn = false">Close</v-btn>
	</v-snackbar>
</v-container>
</template>

<script>

	const updatedMsg = 'Settings are updated. '
	const updatingMsg = 'Settings is updating...'

	export default {

		name: 'setting',

		data() {
			return {
				snackbarOn: false,

				searchSources: [{
					text: 'From DB',
					value: 0,
				}, {
					text: 'From 163',
					value: 1,
				}],

				themes: [{
					text: 'Dark',
					value: true,
				}, {
					text: 'Light',
					value: false,
				}],

				alertMsg: '',
			}
		},

		computed: {
			host: {
				get: function() {
					return this.$store.state.host
				},
				set: function(newValue) {
					this.$store.commit('_update_host', {host: newValue})
				},
			},
			searchSource: {
				get: function() {
					return this.$store.state.searchSource 
				},
				set: function(newValue) {
					this.$store.commit('_update_search_source', {source: newValue})
					this.alertMsg = updatedMsg
					this.snackbarOn = true 
				}
			},
			isDarkTheme: {
				get: function() {
					return this.$store.state.isDarkTheme
				},
				set: function(newValue) {
					this.$store.commit('_update_app_theme', {isDarkTheme: newValue})
				}
			},
			userId: {
				get: function() {
					return this.$store.state.userId
				},
				set: function(newValue) {
					this.$store.commit('_update_user_id', { userId: newValue })
				}
			}
		},

		methods: {
			updateHost() {
				this.alertMsg = updatedMsg
				this.snackbarOn = true 
			},
			updateUserId() {
				this.alertMsg = updatingMsg
				this.snackbarOn = true
				this.$store.dispatch('user/updateUserInfo')
			},
		}
	}
</script>